
* {
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
  overflow-y: scroll;
}
html,
body {
  height: 100vh;
}
body {
  background: #F8EFE7;
}
.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  font-family: Helvetica Neue,Segoe UI,Hiragino Kaku Gothic ProN,Hiragino Sans,ヒラギノ角ゴ ProN W3,Arial,メイリオ,Meiryo,sans-serif;
  line-height: 1.8;
  font-size: 14px;
  box-sizing: border-box;
}
ul,
ol {
  list-style-type: none;
}
a {
  display: block;
  color: #333;
  text-decoration: none;
}
table {
  border-collapse: collapse;
}
h2 {
  border-bottom: 1px solid #D0111B;
  font-size: 30px;
  line-height: 1.4;
}
.contentsWrap {
  background: #ffffff;
  margin: 40px auto 0;
  padding: 30px 5%;
  width: 85%;
  box-sizing: border-box;
}
.contentsWrap + .contentsWrap {
  margin-top: 0;
  padding-top: 20px;
}
.contentsWrap:first-child {
  border-radius: 10px 10px 0 0;
}
.contentsWrap:last-child {
  border-radius: 0 0 10px 10px;
}
@media screen and (max-width: 400px) {
h2 {
    font-size: 20px;
}
}

.party-information-wrap {
  margin-top: 35px;
  display: grid;
  grid-template-columns: 220px 1fr;
  gap: 35px 40px;
  font-size: 16px;
}
.party-information-wrap dt {
  border: 1px solid #D0111B;
  padding: 5px 10px;
  height: fit-content;
  width: 220px;
  text-align: center;
  font-weight: bold;
  color: #D0111B;
  white-space: nowrap;
  box-sizing: border-box;
}
.party-information-wrap dd {
  font-size: 16px;
}
.party-information-wrap a {
  color: #1470CC;
}
.link {
  display: flex;
  align-items: center;
}
.link::before {
  display: inline-block;
  width: 22px;
  height: 22px;
  background: url(/static/img/discord.932bf1d.png) center / contain no-repeat;
  margin-right: 2px;
  content: "";
}
.contents-list > li {
  display: flex;
}
.contents-list > li + li {
  margin-top: 10px;
}
.contents-list > li span + span {
  margin-left: 10px;
}
@font-face {
  font-family: 'WebFont';
  src: url("/static/fonts/amongus vector.d9a380a.ttf");
}
.among-us-font {
  font-family: WebFont;
  font-size: 26px;
  line-height: 1;
}
.preparation-list .note {
  display: flex;
  padding-left: 15px;
  font-size: 12px;
  color: #666666;
  line-height: 1.6;
}
.preparation-list .note::before {
  display: block;
  margin-right: 5px;
  content: "\203B";
}
.preparation-list .note > span {
  display: flex;
  flex-wrap: wrap;
}
.platform-list {
  padding-left: 30px;
  font-size: 12px;
  color: #666666;
  line-height: 1.6;
}
.platform-list li {
  display: flex;
}
.platform-list li::before {
  display: block;
  margin-right: 5px;
  content: "-";
}
.reference-links {
  margin-top: 5px;
  padding-left: 15px;
  font-size: 12px;
  color: #666666;
}
.preparation-list > li + li {
  margin-top: 10px;
}
.help-list {
  padding-left: 15px;
  font-size: 12px;
  line-height: 1.6;
}
@media screen and (max-width: 900px) {
.contentsWrap .party-information-wrap {
    gap: 15px 20px;
}
}
@media screen and (max-width: 750px) {
.contentsWrap .party-information-wrap {
    margin-top: 10px;
    grid-template-columns: 1fr;
    gap: 5px;
}
.contentsWrap .party-information-wrap > dt {
    border: none;
    padding: 0;
    width: fit-content;
}
}
@media screen and (max-width: 400px) {
.party-information-wrap dt {
    font-size: 14px;
}
.party-information-wrap dd {
    font-size: 12px;
}
.platform-list a {
    pointer-events: none;
    color: #666666;
}
}

.footer {
  width: 100%;
  margin-top: 20px;
  padding-bottom: 20px;
}
.footer > .text {
  font-size: 12px;
  letter-spacing: 1.5px;
  color: #4e4e4e;
  text-align: center;
}

@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes scroll {
0% {
    transform: translate(0,0px);
}
100% {
    transform: translate(0,-15px)
}
}
.kv-wrap {
  animation: fadeIn 5s cubic-bezier(0.33, 1, 0.68, 1) 1 forwards;
  position: relative;
}
.kv-wrap.smile,
.kv-wrap:hover::after {
  background-position: -270px 0;
}
.text-area {
  position: relative;
  background: url(/static/kv-bg.png) center / 100% 88% no-repeat;
  height: 100%;
  top: 50%;
  transform: translate3d(0, -50%, 0);
}
.scroll-btn {
  cursor: pointer;
  animation: scroll ease-in-out 1.5s infinite alternate;
}
.scroll-btn  > img,
.scroll-text {
  position: absolute;
  bottom: 0;
  content: '';
  left: 0;
  right: 0;
  margin: auto;
}
.scroll-text {
  top: -43px;
  text-align: center;
}
.kv-title-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  color: #D0111B;
}
.kv-title-wrap > span {
  display: block;
  text-align: center;
}
.kv-title:first-child {
  font-size: 1em;
  line-height: 1;
}
.kv-title:nth-child(2) {
  font-size: 2.5em;
}
.kv-detail {
  margin-top: 12px;
  font-size: 1em;
  font-weight: bold;
  line-height: 1;
}
@media screen and (max-width: 1200px) {
.kv-title:nth-child(2) {
    font-size: 2em;
}
}
@media screen and (max-width: 1140px) {
.kv-title:nth-child(2) {
    font-size: 1.5em;
}
}
@media screen and (max-width: 840px) {
.kv-title:first-child {
    font-size: 0.8em;
}
}
@media screen and (max-width: 795px) {
.kv-title:first-child,
  .kv-detail {
    font-size: 0.6em;
}
.kv-title:nth-child(2) {
    font-size: 1em;
}
}
@media screen and (max-width: 700px) {
.pepa-wrap {
    display: none;
}
.text-area {
    padding: 50px 30px;
    background: #D0111B;
}
.kv-title-wrap {
    background: #ffffff;
    position: initial;
    top: initial;
    left: initial;
    transform: initial;
    padding: 50px 30px;
}
.kv-title:first-child {
    font-size: 1em;
}
.kv-title:nth-child(2) {
    font-size: 2em;
}
.kv-detail {
    margin-top: 10px;
    font-size: 1em;
    font-weight: bold;
}
.kv-wrap {
    height: fit-content !important;
    animation: none;
}
.text-area {
    top: initial;
    transform: initial;
}
.scroll-btn {
    display: none;
}
}
@media screen and (max-width: 645px) {
.kv-title:first-child,
  .kv-detail {
    font-size: 16px;
}
.kv-title:nth-child(2) {
    margin-top: 5px;
    font-size: 23px;
}
}
@media screen and (max-height: 450px) {
.scroll-btn {
    display: none;
}
}
@media screen and (max-width: 400px) {
.kv-title:first-child,
  .kv-detail {
    font-size: 13px;
}
.kv-title:nth-child(2) {
    margin-top: 5px;
    font-size: 20px;
}
}

.participants-wrap {
  flex-wrap: wrap;
  margin-top: 35px;
  display: flex;
  gap: 60px;
}
.participant-img {
  border: 1px solid #cccccc;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  overflow: hidden;
}
.participant-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin: 0 auto;
}
.participant-name {
  font-size: 14px;
  text-align: center;
  margin-top: 10px;
  font-weight: bold;
  letter-spacing: 1.5px;
}
.participant-nickname {
  font-size: 12px;
  text-align: center;
}
@media screen and (max-width: 400px) {
.participants-wrap {
    gap: 10px 8px;
}
.participant-img {
    width: 80px;
    height: 80px;
}
.participant-name {
    width: 80px;
    font-size: 12px;
    line-height: 1.2;
}
.participant-nickname {
    font-size: 10px;
}
}

.talk-group-wrap {
    margin-top: 35px;
    display: flex;
    gap: 20px;
}
.talk-group-wrap li {
  text-align: center;
  width : calc(100% / 2);
}
.talk-group-wrap > li > p {
  font-size: 22px;
  font-weight: bold;
  color: #D0111B;
}
.name-wrap  {
  margin: 5px auto 0;
  width: fit-content;
  white-space: nowrap;
}
.name-wrap li {
  width: fit-content;
  font-size: 16px;
}
.talk-group-wrap > li + li {
  border-left: 1px solid #cccccc;
}
.talk-title-wrap {
  position: relative;
}
.talk-title-wrap > h2 {
  width: 100%;
}
.talk-title-wrap > p {
  position: absolute;
  background: url(/static/update.png) center / 28px no-repeat;
  width: 28px;
  height: 28px;
  top: 50%;
  right: 10px;
  transform: translate3d(0, -50%, 0);
}
.talk-title-wrap > p:hover {
  cursor: pointer;
}
@media screen and (max-width: 400px) {
.talk-group-wrap {
    margin-top: 5px;
}
.talk-group-wrap > li > p {
    font-size: 18px;
}
.name-wrap li {
    font-size: 14px;
}
.talk-title-wrap > p {
    background: url(/static/update.png) center / 22px no-repeat;
    width: 22px;
    height: 22px;
}
}

.ie-wrap {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}
.ie-wrap > .title {
  font-size: 30px;
  font-weight: bold;
}
.ie-wrap > .discription {
  text-align: left;
}
.download-btn {
  background: #D0111B;
  width: fit-content;
  margin: auto;
  border-radius: 8px;
}
.download-btn > a {
  color: #ffffff;
  padding: 20px 40px;
  font-size: 20px;
  line-height: 1;
  font-weight: bold;
}
